import React, { useState, useEffect, useRef } from 'react';
import { Flex, Box } from 'rebass';
import { useStaticQuery, graphql } from 'gatsby';
import GatsbyLink from 'gatsby-link';
import styled from '@emotion/styled';

import ChevronLeft from '../Icons/ChevronLeft';

const MenuBar = styled(Box)`
  overflow: auto;
  height: 100%;
`;

const MainMenu = styled(Box)`
  display: flex;
  position: relative;
  z-index: 2;
`
const MenuItem = styled(Box)`
  padding-right: 2vw; 
  cursor: pointer;
  font-size: 1.2vw;

  &:hover {
    opacity: 0.4;
  }
`;

const SubMenuPointer = styled(Box)`
  display: block;
  position: relative;
  z-index: 4;
  width: 1vw;
  height: 1vw;
  border: 1px solid;
  transform: rotate(45deg);
  border-bottom: 0;
  border-right: 0;
`

const SubMenuWrapper = styled(Box)`
  display: flex;
  position: relative;
  z-index: 3;
  border-top: 1px solid #222;
  padding: 4vw 0;
`

const SubMenu = styled(Box)`
  position: absolute;
  width: 100%;
  left: 50vw;
  top: 50vw;
  box-shadow: 0 5px 10px rgba(0,0,0,0.2);

  a {
    text-align: center;
    cursor: pointer;
    padding: 4vw;
    border-right: 1px solid #e6e6e6;

    &:last-child {
      border-right: 0;
    }

    img {
      transition-property: all;
      transition-duration: 0.3s;
      transition-timing-function: ease-in-out;
      transition-delay: 0s;
      padding: 0 3vw;
    }

    &:hover img {
      transform: scale(1.05);
      transition-property: all;
      transition-duration: 0.3s;
      transition-timing-function: ease-in-out;
      transition-delay: 0s;
    }
  }
`

const Menu = ({ menu: componentMenu }) => {
  const [parentId, setParentId] = useState(0);
  const [showSubmenu, setShowsubmenu] = useState(false);

  const toggleSubmenu = () => {
    setShowsubmenu(!showSubmenu);
  };

  const data = useStaticQuery(graphql`
    {
      menuImages: allShopifyCollection {
        nodes {
          handle
          image {
            src
          }
          fields {
            shopifyThemePath
          }
        }
      }
    }
  `)

  const {
    menuImages: {
      nodes,
    },
  } = data;

  const subCollections = data.menuImages;

  return (
    <MenuBar>
      <MainMenu>
        {componentMenu
          ? componentMenu.map((element) => {
              if (element.parentId === parentId) {
                return (
                  <React.Fragment key={element.id}>
                    {element.type === 'header' ? (
                      <MenuItem bg="menuItem" color="menuText" onClick={toggleSubmenu} key={element.id}>
                        {element.name}
                      </MenuItem>
                    ) : (
                      ''
                    )}
                    {element.type !== 'header' &&
                    element.type !== 'external' ? (
                      <GatsbyLink
                        to={`/${element.type}/${element.handle}`}
                        key={element.id}
                        style={{ textDecoration: 'none' }}
                      >
                        <MenuItem bg="menuItem" color="menuText">
                          {element.name}
                        </MenuItem>
                      </GatsbyLink>
                    ) : (
                      ''
                    )}
                    {element.type === 'external' ? (
                      <a
                        href={element.link}
                        key={element.id}
                        style={{ textDecoration: 'none' }}
                      >
                        <MenuItem bg="menuItem" color="menuText">
                          {element.name}
                        </MenuItem>
                      </a>
                    ) : (
                      ''
                    )}
                  </React.Fragment>
                );
              } else {
                return '';
              }
            })
          : ''}
      </MainMenu>

      {showSubmenu ? (
        <>
          <SubMenu sx={{ transform: ['translate(-50vw, -37vw)', null, 'translate(-50vw, -39vw)']}}>
            <SubMenuPointer bg={'background'} sx={{ left: ['21vw', null, '18vw'], top: '.55vw' }}/>
            <SubMenuWrapper bg="menuItem">
              {componentMenu
                ? componentMenu.map((element) => {
                    if (element.parentId > 0) {
                      return (
                        <React.Fragment key={element.id}>
                          <GatsbyLink
                            to={`/${element.type}/${element.handle}`}
                            key={element.id}
                            style={{ textDecoration: 'none' }}
                          >
                            {subCollections.nodes.map((collection) => {
                              if (collection.handle == element.handle) {
                                return (
                                  <Box key={collection.handle}>
                                    {collection.image ?
                                      <img src={collection.image.src} alt={collection.handle} style={{ width: "100%"}} />
                                    : "" }
                                  </Box>
                                )
                              } else {
                                return ""
                              }
                            })}
                            <MenuItem bg="menuItem" color="menuText" pr={'0!important'}>
                              {element.name}
                            </MenuItem>
                          </GatsbyLink>
                        </React.Fragment>
                      );
                    } else {
                      return '';
                    }
                  })
                : ''}
            </SubMenuWrapper>
          </SubMenu>
        </>
      ) : (
        ''
      )}
    </MenuBar>
  );
};

export default Menu;
