/** @jsx jsx */
/*eslint no-unused-vars: 0*/
import React from 'react';
import GatsbyImage from 'gatsby-image';
import styled from '@emotion/styled/macro';
import { jsx } from 'theme-ui';
import { Box, Button } from 'rebass';
import { Form } from 'react-bootstrap';
import strings from './strings.json';

const { ariaNewsletterHeader, ariaSubscribeLabel, ariaEmailPlaceholder } = strings;

const Flag = styled(Box)`
  content: '';
  position: absolute;
  background: transparent;
  left: 0;
  transform: translate(0, 0);
  width: inherit;
  height: 0;
  top: 0;
  border-left: 50vw solid white;
  border-right: 50vw solid white;
`;

const Newsletter = () => {
  return (
    <Box mb={50} pt={[50, 75, 100]} pb={[60, 60, 80]} bg={"backgroundGrey"} sx={{ position: 'relative' }}>
      <Flag sx={{ borderBottom: ['25px solid transparent', '50px solid transparent', '75px solid transparent'], margin: ['0 -5vw', '0 -3vw', '0 -3.5vw'] }}></Flag>
      <Form sx={{ textAlign: 'center' }}>
        <Box fontSize={['6vw', '3vw', '2.5vw']} mb={3} lineHeight={['2.22', '1.67', '1.5']}>{ ariaNewsletterHeader }</Box>
        
        <Box mx={[20, 'auto']} p={10} sx={{ display: ['block', 'flex'], bg: 'background', position: 'relative', zIndex: '1', width: ['auto', '85%', '50%'] }}>
          <input type="email" className="form-control form-control-lg border-0" sx={{ width: ['100%', 'inherit'], margin: 'auto', marginLeft: 0, fontSize: ['5vw', '2vw', '1.2vw'], textAlign: ['center', 'left'] }} id="exampleInputEmail1" aria-describedby="emailHelp" placeholder={ ariaEmailPlaceholder } />
          <Button type="submit" bg={"primary"} className="rounded-0 text-uppercase" sx={{ width: ['100%', 'auto'], fontWeight: '500', fontSize: ['4vw', '1.5vw', '1.1vw'], letterSpacing: ['.6vw', '.4vw', '.2vw'], padding: ['4vw', '2vw 4vw', '1vw 2vw'] }}>{ ariaSubscribeLabel }</Button>
        </Box>
        
        <img src="//cdn.shopify.com/s/files/1/0117/9005/7534/files/fruit2-3_90x90@2x.png" alt="" sx={{ position: 'absolute', transform: 'translate(-50%, -50%)', left: '50%', bottom: ['-50px', '-50px', '-80px'], zIndex: 0, width: ['120px', '120px', '180px'] }} />
      </Form>
    </Box>
  );
};

export default Newsletter;