import React, { useState, useEffect, useRef } from 'react';
import { Flex, Box, Text } from 'rebass';
import { useStaticQuery, graphql } from 'gatsby';
import GatsbyLink from 'gatsby-link';
import styled from '@emotion/styled';
import {
  disableBodyScroll,
  enableBodyScroll,
  clearAllBodyScrollLocks,
} from 'body-scroll-lock';

import { useMenuContext } from './context';
import ChevronLeft from '../Icons/ChevronLeft';
import Burger from '../Icons/Burger';
import Close from '../Icons/Close';

const Sidebar = styled(Box)`
  position: absolute;
  z-index: 2;
  top: 19vw;
  left: 0;
  height: auto;
  padding: 0 5vw;
  border-top: 2px solid #222;
  box-shadow: 0 5px 10px rgba(0,0,0,0.2);
`;

const MenuPointer = styled(Box)`
  display: block;
  position: absolute;
  z-index: 1;
  width: 15px;
  height: 15px;
  left: 6vw;
  top: 17.5vw;
  background: #222;
  transform: rotate(45deg);
  border-bottom: 0;
  border-right: 0;
`

const TopMenu = styled(Box)`
  display: flex;
  flex-direction: column;
  padding: 3vw 0;

  a {
    font-size: 2.5vw;
    text-transform: uppercase;
  }
`

const SubMenuWrapper = styled(Box)`
  position: absolute;
  top: 0;
  left: 0;
  height: inherit;
  box-shadow: 0 5px 10px rgba(0,0,0,0.2);

  a {
    display: flex;
    text-align: center;
    padding: 0 5vw;
    border-top: 1px solid #e6e6e6;
    cursor: pointer;

    img {
      padding: 0 5vw;
      width: 30%;
      transition-property: all;
      transition-duration: 0.3s;
      transition-timing-function: ease-in-out;
      transition-delay: 0s;
    }

    &:hover img {
      transform: scale(1.05);
      transition-property: all;
      transition-duration: 0.3s;
      transition-timing-function: ease-in-out;
      transition-delay: 0s;
    }

    div {
      padding: 0;
      border-bottom: none;
    }
  }
`

const BackButton = styled(Box)`
  width: fit-content;
  border-right: 1px solid rgba(0,0,0,.1);
  padding: 4vw;
`

const SubMenu = styled(Box)`
  display: flex;
  flex-direction: column;
  z-index: 3;
`

const MenuItem = styled(Box)`
  font-size: 4vw;
  padding-top: 3vw;
  padding-bottom: 3vw;
  margin: auto 0;
  border-bottom: 1px solid rgba(0,0,0,.1);

  &:hover {
    opacity: 0.4;
  }
`;

const validURL = (str) => {
  const pattern = new RegExp(
    '^(https?:\\/\\/)?' + // protocol
    '((([a-z\\d]([a-z\\d-]*[a-z\\d])*)\\.)+[a-z]{2,}|' + // domain name
    '((\\d{1,3}\\.){3}\\d{1,3}))' + // OR ip (v4) address
    '(\\:\\d+)?(\\/[-a-z\\d%_.~+]*)*' + // port and path
    '(\\?[;&a-z\\d%_.~+=-]*)?' + // query string
      '(\\#[-a-z\\d_]*)?$',
    'i'
  ); // fragment locator
  return !!pattern.test(str);
};

const MobileMenu = ({ menu: componentMenu }) => {
  const [parentId, setParentId] = useState(0);
  const [showSidebar, setShowsidebar] = useState(false);
  const [showSubmenu, setShowsubmenu] = useState(false);

  const toggleSidebar = () => {
    setShowsidebar(!showSidebar);
  };

  const toggleSubmenu = () => {
    setShowsubmenu(!showSubmenu);
  };

  const data = useStaticQuery(graphql`
    {
      menuImages: allShopifyCollection {
        nodes {
          handle
          image {
            src
          }
          fields {
            shopifyThemePath
          }
        }
      }
      site {
        siteMetadata {
          gatsbyStorefrontConfig {
            topNavMenu {
              link
              name
            }
          }
        }
      }
    }
  `)

  const {
    menuImages: {
      nodes,
    },
  } = data;

  const subCollections = data.menuImages;

  const {topNavMenu}  = data.site.siteMetadata.gatsbyStorefrontConfig;

  return (
    <>
      <Burger
        width="20px"
        height="20px"
        onClick={toggleSidebar}
        sx={{ cursor: 'pointer' }}
      />

      {showSidebar ? (
        <>
          <MenuPointer />
          <Sidebar
            width={[1, 1 / 3, 1 / 4, 1 / 5]}
            sx={{ bg: 'menu' }}
          >
            <Flex flexDirection="column">
              {componentMenu
                ? componentMenu.map((element) => {
                    if (element.parentId === parentId) {
                      return (
                        <React.Fragment key={element.id}>
                          {element.type === 'header' ? (
                            <MenuItem
                              sx={{ cursor: 'pointer' }}
                              bg="menuItem"
                              color="menuText"
                              onClick={toggleSubmenu}
                              fontSize={[3]}
                              key={element.id}
                            >
                              {element.name}
                            </MenuItem>
                          ) : (
                            ''
                          )}
                          {element.type !== 'header' &&
                          element.type !== 'external' ? (
                            <GatsbyLink
                              to={`/${element.type}/${element.handle}`}
                              key={element.id}
                              style={{ textDecoration: 'none' }}
                            >
                              <MenuItem
                                bg="menuItem"
                                color="menuText"
                                fontSize={[3]}
                              >
                                {element.name}
                              </MenuItem>
                            </GatsbyLink>
                          ) : (
                            ''
                          )}
                          {element.type === 'external' ? (
                            <a
                              href={element.link}
                              key={element.id}
                              style={{ textDecoration: 'none' }}
                            >
                              <MenuItem
                                bg="menuItem"
                                color="menuText"
                                fontSize={[2, 3]}
                              >
                                {element.name}
                              </MenuItem>
                            </a>
                          ) : (
                            ''
                          )}
                        </React.Fragment>
                      );
                    } else {
                      return '';
                    }
                  })
                : ''}
            </Flex>

            {showSubmenu ? (
              <>
                <SubMenuWrapper bg="menuItem" color="menuText">
                  <BackButton>
                    <ChevronLeft
                      width="25px"
                      height="25px"
                      onClick={toggleSubmenu}
                      sx={{ cursor: 'pointer' }}
                    />
                  </BackButton>
                  
                  <SubMenu>
                    {componentMenu
                      ? componentMenu.map((element) => {
                          if (element.parentId > 0) {
                            return (
                              <React.Fragment key={element.id}>
                                <GatsbyLink
                                  to={`/${element.type}/${element.handle}`}
                                  key={element.id}
                                  style={{ textDecoration: 'none' }}
                                >
                                  {subCollections.nodes.map((collection) => {
                                    if (collection.handle == element.handle) {
                                      return (
                                        <Box width={[1 / 3]} key={collection.handle}>
                                          {collection.image ?
                                            <img src={collection.image.src} alt={collection.handle} style={{ width: "100%"}} />
                                          : "" }
                                        </Box>
                                      )
                                    } else {
                                      return ""
                                    }
                                  })}
                                  <MenuItem>
                                    {element.name}
                                  </MenuItem>
                                </GatsbyLink>
                              </React.Fragment>
                            );
                          } else {
                            return '';
                          }
                        })
                      : ''}
                  </SubMenu>
                </SubMenuWrapper>
              </>
            ) : (
              ''
            )}

            <TopMenu>
              {topNavMenu
                ? topNavMenu.map((link, index) => {
                  // If link is valid url use <a>
                  // else use gatsby-link
                  if (validURL(link.link)) {
                    return (
                      <Text key={index} mr={['4vw', '2vw']}> 
                        <Link href={link.link}>{link.name}</Link>
                      </Text>
                    );
                  } else {
                    return (
                      <Text key={index} mr={['4vw', '2vw']}>
                        <GatsbyLink to={link.link}>
                          {link.name}
                        </GatsbyLink>
                      </Text>
                    );
                  }
                })
              : ''}
            </TopMenu>
          </Sidebar>
        </>
      ) : (
        ''
      )}
    </>
  );
};

export default MobileMenu;
