/** @jsx jsx */
/* eslint no-unused-vars: 0 */
import React from 'react';
import { jsx, useThemeUI } from 'theme-ui';
import { Flex, Box, Text, Link } from 'rebass';
import { useStaticQuery, graphql } from 'gatsby';
import GatsbyLink from 'gatsby-link';
import styled from '@emotion/styled';
import loadable from '@loadable/component';
// import { SocialIcon } from 'react-social-icons';
import Gps from './Icons/Gps';
import Phone from './Icons/Phone';
import Email from './Icons/Email';
import Payments from '../components/Payments';
import Accordion from './Accordion/Accordion';
import Newsletter from './Newsletter';

const SocialIcon = loadable(() => import('./SocialIcon'));

const Header = styled(Text)`
  margin-bottom: 1.5vw;
  font-weight: 500;
`

const Content = styled(Text)`
  margin-top: .5vw;
  display: flex;
`

const validURL = (str) => {
  const pattern = new RegExp(
    '^(https?:\\/\\/)?' + // protocol
    '((([a-z\\d]([a-z\\d-]*[a-z\\d])*)\\.)+[a-z]{2,}|' + // domain name
    '((\\d{1,3}\\.){3}\\d{1,3}))' + // OR ip (v4) address
    '(\\:\\d+)?(\\/[-a-z\\d%_.~+]*)*' + // port and path
    '(\\?[;&a-z\\d%_.~+=-]*)?' + // query string
      '(\\#[-a-z\\d_]*)?$',
    'i'
  ); // fragment locator
  return !!pattern.test(str);
};

function Footer() {
  const data = useStaticQuery(graphql`
    query FooterQuery {
      site {
        siteMetadata {
          gatsbyStorefrontConfig {
            email
            company
            location
            street
            shipper
            phone
            socialNetworks
            payments
            footerLinksShop {
              name
              link
            }
            footerLinksShea {
              name
              link
            }
            footerLinksInfo {
              name
              link
            }
          }
        }
      }
    }
  `);

  const {
    email,
    company,
    location,
    street,
    shipper,
    phone,
    socialNetworks,
    payments,
    footerLinksShop,
    footerLinksShea,
    footerLinksInfo,
  } = data.site.siteMetadata.gatsbyStorefrontConfig;

  const { theme } = useThemeUI();

  const year = new Date().getFullYear();

  return (
    <Box bg="background" mt={[2, 3, 4]} py={[1, 2, 3, 4]} px={['5vw', '3vw', '3.5vw']}>
      <Newsletter />
      <Box
        as="footer"
        width={1}
        mx="auto"
      >
        <Box display={['none', 'flex']} width={[1]} alignItems="center" mb={[2, 3, 4]} flexWrap="wrap" justifyContent={['center', 'space-between']} flexWrap="wrap" >
          <Box width={[1, 1 / 4, 1 / 5]} mb={["auto"]} fontSize={[null, '1.5vw', '1.2vw']}>
            <Header>Institut Karité Paris</Header>
            <Content>
              <Gps width={['4vw', null, '2vw']} />
              <Text>{street ? '  ' : ''} {street}<br /> {shipper ? '  ' : ''} {shipper}<br /> {location ? '  ' : ''} {location}</Text>
            </Content>
            <Content>
              <Phone width={['4vw', null, '2vw']} />
              <Text>{phone ? '  ' : ''} {phone}</Text>
            </Content>
            <Content>
              <Email width={['4vw', null, '2vw']} />
              <Text>{email ? '  ' : ''} {email}</Text>
            </Content>
          </Box>

          <Box width={[1, "auto"]} mb={["auto"]} fontSize={[null, '1.5vw', '1.2vw']}>
            <Header>Shop</Header>
            {footerLinksShop
              ? footerLinksShop.map((link, index) => {
                // If link is valid url use <a>
                // else use gatsby-link
                if (validURL(link.link)) {
                  return (
                    <Content key={index}>
                      <Link href={link.link}>{link.name}</Link>
                    </Content>
                  );
                } else {
                  return (
                    <Content key={index}>
                      <GatsbyLink to={link.link} sx={theme.variants.link}>
                        {link.name}
                      </GatsbyLink>
                    </Content>
                  );
                }
              })
            : ''}
          </Box>

          <Box width={[1, "auto"]} mb={["auto"]} fontSize={[null, '1.5vw', '1.2vw']}>
            <Header>House of Shea</Header>
            {footerLinksShea
              ? footerLinksShea.map((link, index) => {
                // If link is valid url use <a>
                // else use gatsby-link
                if (validURL(link.link)) {
                  return (
                    <Content key={index}>
                      <Link href={link.link}>{link.name}</Link>
                    </Content>
                  );
                } else {
                  return (
                    <Content key={index}>
                      <GatsbyLink to={link.link} sx={theme.variants.link}>
                        {link.name}
                      </GatsbyLink>
                    </Content>
                  );
                }
              })
            : ''}
          </Box>

          <Box width={[1, "auto"]} mb={["auto"]} fontSize={[null, '1.5vw', '1.2vw']}>
            <Header>Information</Header>
            {footerLinksInfo
              ? footerLinksInfo.map((link, index) => {
                // If link is valid url use <a>
                // else use gatsby-link
                if (validURL(link.link)) {
                  return (
                    <Content key={index}>
                      <Link href={link.link}>{link.name}</Link>
                    </Content>
                  );
                } else {
                  return (
                    <Content key={index}>
                      <GatsbyLink to={link.link} sx={theme.variants.link}>
                        {link.name}
                      </GatsbyLink>
                    </Content>
                  );
                }
              })
            : ''}
          </Box>
          
          <Box display={['none', null, 'flex']} width={[ 1 / 10]} mb={["auto"]} flexDirection={['column']} justifyContent={['center']} fontSize={[null, '2vw', '1.2vw']}>
            <Header>Follow Us</Header>
            {socialNetworks
              ? socialNetworks.map((socialNetwork, index) => {
                  return (
                    <Box key={index}>
                      <SocialIcon url={socialNetwork} bgColor={theme.colors.primary}/>
                    </Box>
                  );
                })
              : ''}
          </Box>
        </Box>

        {/* Footer Links Accordion */}
        <Box width={1} display={['block', 'none']}>
          <Accordion allowMultipleOpen>
            <div label='Institut Karité Paris'>
              <Content>
                <Gps width={['4vw', null, '2vw']} />
                <Text>{street ? '  ' : ''} {street}<br /> {shipper ? '  ' : ''} {shipper}<br /> {location ? '  ' : ''} {location}</Text>
              </Content>
              <Content>
                <Phone width={['4vw', null, '2vw']} />
                <Text>{phone ? '  ' : ''} {phone}</Text>
              </Content>
              <Content>
                <Email width={['4vw', null, '2vw']} />
                <Text>{email ? '  ' : ''} {email}</Text>
              </Content>
            </div>

            <div label='Shop'>
              {footerLinksShop ? footerLinksShop.map((link, index) => {
                // If link is valid url use <a>
                // else use gatsby-link
                if (validURL(link.link)) {
                  return (
                    <Text key={index} py={"5px"}>
                      <Link href={link.link}>{link.name}</Link>
                    </Text>
                  );
                } else {
                  return (
                    <Text key={index} py={"5px"}>
                      <GatsbyLink to={link.link} sx={theme.variants.link}>
                      {link.name}
                    </GatsbyLink>
                  </Text>
                  );
                }
              })
            : ''}
            </div>

            <div label='The House of Shea'>
              {footerLinksShea ? footerLinksShea.map((link, index) => {
                // If link is valid url use <a>
                // else use gatsby-link
                if (validURL(link.link)) {
                  return (
                    <Text key={index} py={"5px"}>
                      <Link href={link.link}>{link.name}</Link>
                    </Text>
                  );
                } else {
                  return (
                    <Text key={index} py={"5px"}>
                      <GatsbyLink to={link.link} sx={theme.variants.link}>
                      {link.name}
                    </GatsbyLink>
                  </Text>
                  );
                }
              })
            : ''}
            </div>

            <div label='Information'>
              {footerLinksInfo ? footerLinksInfo.map((link, index) => {
                // If link is valid url use <a>
                // else use gatsby-link
                if (validURL(link.link)) {
                  return (
                    <Text key={index} py={"5px"}>
                      <Link href={link.link}>{link.name}</Link>
                    </Text>
                  );
                } else {
                  return (
                    <Text key={index} py={"5px"}>
                      <GatsbyLink to={link.link} sx={theme.variants.link}>
                      {link.name}
                    </GatsbyLink>
                  </Text>
                  );
                }
              })
            : ''}
            </div>
          </Accordion>
        </Box>

        <Box width={'30vw'} mx={'auto'} mt={'5vw'} display={['flex', 'none']} justifyContent={'space-evenly'}>
          {socialNetworks
            ? socialNetworks.map((socialNetwork, index) => {
              return (
                <Box key={index}>
                  <SocialIcon url={socialNetwork} bgColor={theme.colors.primary}/>
                </Box>
              );
            })
          : ''}
        </Box>
        
        <Box width={1} display={['block', 'flex', null]}>
          <Box width={[1, '45vw', '80vw']} ml={['auto', 0]} mr={'auto'} my={25}>
            <Text fontSize={['3vw', '1.5vw', '1vw']} textAlign={['center', 'left']}>
              <span>© 2018 - {year} Tous droits réservés. {company ? company : ''} <a href="https://www.port80webdesign.com/">ecommerce by port80</a></span>
            </Text>
          </Box>

          <Box width={'15vw'} m={'auto'} display={[ 'none', 'flex', 'none' ]} justifyContent={'space-evenly'}>
            {socialNetworks
              ? socialNetworks.map((socialNetwork, index) => {
                return (
                  <Box key={index}>
                    <SocialIcon url={socialNetwork} bgColor={theme.colors.primary}/>
                  </Box>
                );
              })
            : ''}
          </Box>

          <Box width={['60vw', '30vw', '20vw']} mr={['auto', 0]} ml={'auto'} my={25}>
            <Payments payments={payments} />
          </Box>
        </Box>
      </Box>
    </Box>
  );
}

export default React.memo(Footer);
