import React, { useState } from 'react';
import { Flex, Text, Box } from 'rebass';
import { useStaticQuery, graphql } from 'gatsby';
import { Link as GatsbyLink } from 'gatsby';
import styled from '@emotion/styled';
import { useScrollPosition } from '@n8tb1t/use-scroll-position';

import Menu from './Menu';
import MobileMenu from './MobileMenu';
import Account from './Icons/Account';
import Search from './Search';
import ShoppingBag from './Icons/ShoppingBag';
import strings from './strings.json';
import formatPrice from '../utils/formatPrice';
import useShopifyFunctions from '../hooks/useShopifyFunctions';

const { ariaShoppingCartLabel, ariaHomaPageLinkLabel } = strings;

const NavbarBase = styled.nav`
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1030;
`;

const Nav = styled(NavbarBase)`
  box-shadow: 0 0px 10px rgba(0, 0, 0, 0.2);
  visibility: ${(props) => (props.show ? 'visible' : 'hidden')};
  transition: all 200ms ${(props) => (props.show ? 'ease-in' : 'ease-out')};
  transform: ${(props) => (props.show ? 'none' : 'translate(0, -100%)')};
`;

const validURL = (str) => {
  const pattern = new RegExp(
    '^(https?:\\/\\/)?' + // protocol
    '((([a-z\\d]([a-z\\d-]*[a-z\\d])*)\\.)+[a-z]{2,}|' + // domain name
    '((\\d{1,3}\\.){3}\\d{1,3}))' + // OR ip (v4) address
    '(\\:\\d+)?(\\/[-a-z\\d%_.~+]*)*' + // port and path
    '(\\?[;&a-z\\d%_.~+=-]*)?' + // query string
      '(\\#[-a-z\\d_]*)?$',
    'i'
  ); // fragment locator
  return !!pattern.test(str);
};

const Navbar = (props) => {
  const [hideNavbarOnScroll, setHideNavbarOnScroll] = useState(true);

  useScrollPosition(
    ({ prevPos, currPos }) => {
      // Note: prevPos.y > -12 is here to fix Nav component disappearing bug
      // due to elastic scrolling/bounce effect in mobile Safari.
      const isShow = currPos.y > prevPos.y || prevPos.y > -12;
      if (isShow !== hideNavbarOnScroll) setHideNavbarOnScroll(isShow);
    },
    [hideNavbarOnScroll],
    null,
    false,
    100
  );

  const data = useStaticQuery(graphql`
    {
      site {
        siteMetadata {
          gatsbyStorefrontConfig {
            storeName
            logoUrl
            menu {
              handle
              id
              link
              name
              parentId
              type
            }
            topNavMenu {
              link
              name
            }
            locales
            currency
          }
        }
      }
    }
  `);

  const {
    storeName,
    logoUrl,
    menu,
    topNavMenu,
    locales,
    currency
  } = data.site.siteMetadata.gatsbyStorefrontConfig;

  const { checkout } = useShopifyFunctions();
  const { subtotalPrice } = checkout;

  const displaySubtotalPrice = formatPrice(
    Number(subtotalPrice),
    locales,
    currency
  );

  return (
    <Nav show={hideNavbarOnScroll} style={{ padding: ['0 3vw'], background: 'white', margin: ['3vw'] }}>
      {/* Mobile Nav */}
      <Box py={3} width={1} as="nav" display={['block', 'none']}>
        <Flex justifyContent="center" alignItems="center" mx="auto" px='2vw'>
          <Box width='auto' pr='2vw' mr='auto'>
            <MobileMenu menu={menu} />
          </Box>

          <GatsbyLink aria-label={ariaShoppingCartLabel} to="/account" style={{ textDecoration: 'none', width: 'auto', padding: '0 2vw', marginRight: 'auto' }}>
            <Account my={'auto'} width={25} height={25} disabled/>
          </GatsbyLink>

          <GatsbyLink to="/" aria-label={ariaHomaPageLinkLabel} style={{ textDecoration: 'none', width: '30vw', padding: '0 2vw', margin: '0 auto' }}>
            {logoUrl ? (
              <img src={logoUrl} alt={storeName} style={{ width: '100%' }}/>
            ) : (
              <>
                <ShoppingBag
                  m="auto"
                  width={['25px', '30px']}
                  height={['25px', '30px']}
                  color="secondary"
                />

                <Text
                  color="primary"
                  fontSize={[2, 3]}
                  sx={{ display: ['none', 'block'] }}
                >
                  {storeName}
                </Text>
              </>
            )}
          </GatsbyLink>

          <Box width='auto' px='2vw' ml='auto'>
            <Search width={25} height={25} color="primary" />
          </Box>

          <GatsbyLink aria-label={ariaShoppingCartLabel} to="/cart" style={{ textDecoration: 'none', width: 'auto', padding: '0 0 0 2vw', marginLeft: 'auto' }}>
            <ShoppingBag width={25} height={25} color="primary" />
          </GatsbyLink>
        </Flex>
      </Box>

      {/* Desktop Nav */}
      <Box width={1} as="nav" display={['none', 'block']}>
        <Box py={'1vw'} sx={{ borderBottom: '1px solid rgb(230, 230, 230)', fontSize: ['1.2vw', null, '.8vw'], textTransform: 'uppercase' }} alignItems="left">
          <Flex>
            {topNavMenu
              ? topNavMenu.map((link, index) => {
                // If link is valid url use <a>
                // else use gatsby-link
                if (validURL(link.link)) {
                  return (
                    <Text key={index} mr={['4vw', '2vw']}> 
                      <Link href={link.link}>{link.name}</Link>
                    </Text>
                  );
                } else {
                  return (
                    <Text key={index} mr={['4vw', '2vw']}>
                      <GatsbyLink to={link.link}>
                        {link.name}
                      </GatsbyLink>
                    </Text>
                  );
                }
              })
            : ''}
          </Flex>
        </Box>
        
        <Flex py={'1vw'} fontSize={[null, '1.7vw', '1.2vw']}>
          <Box width={['25vw', '15vw', '12vw']}>
            <GatsbyLink to="/" aria-label={ariaHomaPageLinkLabel} style={{ textDecoration: 'none' }}>
              {logoUrl ? (
                <img src={logoUrl} alt={storeName} style={{ margin: '1vw 0', paddingRight: '2vw', width: '100%' }}/>
              ) : (
                <>
                  <ShoppingBag
                    m="auto"
                    width={['25px', '30px']}
                    height={['25px', '30px']}
                    color="secondary"
                  />

                  <Text
                    color="primary"
                    fontSize={[2, 3]}
                    sx={{ display: ['none', 'block'] }}
                  >
                    {storeName}
                  </Text>
                </>
              )}
            </GatsbyLink>
          </Box>
          
          <Box width={'auto'} my={'auto'} px={'2vw'} py={'1vw'} sx={{ borderLeft: '1px solid rgb(230, 230, 230)' }}>
            <Menu menu={menu} />
          </Box>

          <Flex my={'auto'} ml="auto" py={'1vw'} width={'auto'}>
            <Box pr={'2vw'}>
              <Search my={'auto'} width={[null, '2vw', '1.7vw']} height={[null, '2vw', '1.7vw']} color="primary" />
            </Box>

            <GatsbyLink aria-label={ariaShoppingCartLabel} to="/account" style={{ display: 'flex', borderLeft: '1px solid rgb(230, 230, 230)', padding: '0 2vw', textDecoration: 'none' }}>
              <Account my={'auto'} width={[null, '2vw', '1.7vw']} height={[null, '2vw', '1.7vw']} color="primary" disabled/>
              <Text ml={'1vw'} sx={{ display: ['none', null, 'block'] }}>Log In</Text>
            </GatsbyLink>

            <GatsbyLink aria-label={ariaShoppingCartLabel} to="/cart" style={{ display: 'flex', borderLeft: '1px solid rgb(230, 230, 230)', padding: '0 0 0 2vw', textDecoration: 'none' }}>
              <ShoppingBag my={'auto'} width={[null, '2vw', '1.7vw']} height={[null, '2vw', '1.7vw']} color="primary" />
              <Text ml={'1vw'} sx={{ display: ['none', null, 'block'] }}>{ displaySubtotalPrice }</Text>
            </GatsbyLink>
          </Flex>
        </Flex>
      </Box>
    </Nav>
  );
};

export default React.memo(Navbar);
